import React, { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { userSchema } from "./user-form.schema";
import { userFormConfig } from "./user-form-config";
import { FormProvider } from "@/components/form/context/FormProvider";
import { getController } from "@/components/form/utils/getController";
import FormFieldRow from "@/components/form/wrapper/FormFieldRow";
import FieldWrapper from "@/components/form/wrapper/FieldWrapper";
import Spacer from "@/components/form/wrapper/Spacer";
import TableDataLoader from "@/components/common/dynamic-table/TableDataLoader";
import { useTablePagination } from "@/components/common/dynamic-table/hooks/useTablePagination";
import axios from "axios";
import { FormContentWrapper } from "@/components/form/wrapper/FormContentWrapper";
import { useCreateUser } from "../../../hooks/useCreateUser";
import { useUpdateUser } from "@/features/co-admin/hooks/useUserUpdate";
import { usePageTitle } from "@/hooks/usePageTitle";
import { useParams, useLocation } from "react-router-dom";
import { Edit } from "lucide-react";
import Switch from "@/components/ui/switch";
import { NorthEast, SouthWest } from '@mui/icons-material';
import { toast } from "sonner";
import { ConfirmationAlert } from "@/components/ui/ConfirmationAlert";
import { ClipboardCopy, MoveLeft, ArrowLeft } from "lucide-react";

import { getUserTableColumns4 } from "../n-user-table/n-user-creation-table-col";
// import { getUserTableColumns4 } from "../n-user-creation-table-col";
import { cn } from "@/utils/cn";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import PhoneInput from "@/components/ui/phoneinput"; // ✅ Correct for default exports
import { Input } from "@/components/ui/input";
import ViewAllTable from "../n-user-table/ViewAllTable";
import ViewAllTable2 from "../n-user-table/ViewAllTable2";
import NavigateWrapper from "@/features/auth/components/NavigateWrapper";
const useScreenSize = () => {
  const [screenWidth, setScreenWidth] = useState(window.innerWidth);
  useEffect(() => {
    const handleResize = () => setScreenWidth(window.innerWidth);
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);
  return screenWidth;
};

const UserCreationFormPage = () => {
  const screenWidth = useScreenSize();
  const { id } = useParams();
  // const isEditMode = !!id;
  const isEditMode = false;
  const { setTitle } = usePageTitle();
  const location = useLocation();
  const selectedRow = (location.state as any)?.selectedRow || null;

  useEffect(() => {
    setTitle(isEditMode ? "Edit Client" : "Create Client");
  }, [setTitle]);

  const methods = useForm({
    resolver: zodResolver(userSchema),
    defaultValues: {
      first_name: "",
      last_name: "",
      email: "",
      phone_number: "",
      password: "",
      mac_address: "",
      role_id: 1,
      started_on: "",
    },
  });

  const {
    control,
    reset,
    formState: { errors, isSubmitting },
    handleSubmit,
  } = methods;

  const { mutate: createUser, isLoading } = useCreateUser({
    onUserCreateSuccess: (data) => {
      reset({});
    },
  });
  const { mutate: updateUser, isLoading: isUpdating } = useUpdateUser({
    user_id: Number(id), // pass it once here
    onUserUpdateSuccess: (data) => {
      console.log(data);
      reset({});
    },
  });

  const [userData, setUserData] = useState([])
  const [loading, setLoading] = useState(false)

  const [workspaceData, setWorkspaceData] = useState([])
  const fetchUserDetails = async () => {
    setLoading(true)
    // Fetch full user details
    const response = await axios.get(`http://localhost:8001/api/user/${selectedRow.id}/`);

    const data = response.data.results;
    setUserData(data);
    setLoading(false)
  }

  const fetchWorkspaceDetails = async () => {
    setLoading(true)
    // Fetch full user details
    const response = await axios.get(`http://localhost:8001/api/workspace/?created_by=${selectedRow.id}`);

    const data = response.data.results;
    setWorkspaceData(data);
    setLoading(false)
  }


  useEffect(() => {
    fetchWorkspaceDetails()
    fetchUserDetails();
  }, [])

  useEffect(() => {
    if (selectedRow && Object.keys(selectedRow).length > 0) {
      reset({
        first_name: selectedRow.first_name || "",
        last_name: selectedRow.last_name || "",
        email: selectedRow.email || "",
        phone_number: selectedRow.phone_number || "", // still input as string in form
        mac_address: selectedRow.mac_address || "",
        role_id: selectedRow.role_id || 1,
        started_on: selectedRow.started_on || "",
      });
    }
    fetchUserDetails();
    fetchWorkspaceDetails()
  }, [selectedRow, reset]);

  const handleFormSubmit = handleSubmit(async (formData) => {
    const payload = {
      // id: Number(id), // Use id from useParams
      first_name: formData.first_name,
      last_name: formData.last_name,
      email: formData.email,
      phone_number: formData.phone_number, // Convert to number
      // password: formData.password || null,
      mac_address: formData.mac_address || null,
      role_id: formData.role_id,
      is_active: true, // Adjust based on your needs
      started_on: formData.started_on,
    };
    console.log(payload)
    if (isEditMode) {
      console.log(payload)
      updateUser(payload);
    }
    else {
      createUser({
        first_name: formData.first_name,
        last_name: formData.last_name,
        email: formData.email,
        phone_number: Number(formData.phone_number), // Converts string to number
        password: formData.password || null,
        mac_address: formData.mac_address || null,
        // role_id: 1,
        role_id: formData.role_id,
        is_active: true,
        started_on: formData.started_on,
      });
    }
  });


  const showDanger = (workspace: any) =>
    !workspace.is_activated && workspace.failed_activation_attempts > 2;
  const handleUserStatusChange = async (userId: number, newStatus: boolean | null) => {
    try {
      const response = await fetch(`http://localhost:8001/api/user/${userId}/`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        body: JSON.stringify({
          is_authorized: newStatus,
        }),
      });

      if (!response.ok) {
        throw new Error("Failed to update user status");
      }

      const data = await response.json();
      console.log("Updated:", data);
      fetchUserDetails()
      // optionally: refresh the user data or show a toast message
    } catch (error) {
      console.error("Error updating user status:", error);
      alert("Something went wrong while updating user status.");
    }
  };

  const [fieldAssistants, setFieldAssistants] = useState([]);
const [fieldAssistantId, setFieldAssistantId] = useState("");

console.log(fieldAssistants)

useEffect(() => {
  fetch("http://localhost:8001/api/user/?role=3")
    .then((res) => res.json())
    .then((data) => setFieldAssistants(data.results))
    .catch((err) => console.error("Failed to fetch users", err));
}, []);


  const handleGenerateKey = async (workspaceId: number) => {
    setLoading(true);
    try {
      const res = await fetch("http://localhost:8001/api/workspace-activate/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          workspace_id: workspaceId,
          field_assistant: Number(fieldAssistantId),
        }),
      });

      const data = await res.json();
      setWorkspaceData((prev) =>
        prev.map((workspace) =>
          workspace.id === workspaceId
            ? {
              ...workspace,
              activation_key: data.activation_key,
              activation_key_expiry: data.activation_key_expiry,
              field_assistant: data.field_assistant_id,
            }
            : workspace
        )
      );
      alert(`✅ ${data.message} - Key: ${data.activation_key}`);
    } catch (error) {
      alert("❌ Activation failed");
    } finally {
      setLoading(false);
    }
  };


  return (
    <>
      {loading ? <>
        <div className="flex justify-center items-center min-h-screen flex-col">


          <div className="flex flex-row space-x-4">
            <div className="w-12 h-12 rounded-full animate-spin
                    border-2 border-solid border-blue-500 border-t-transparent"></div>
            <h1>Loading...</h1>
          </div>
        </div>

      </> : <>
        <FormProvider methods={methods}>
          <form onSubmit={handleFormSubmit}>
            <Spacer>
              <div className="flex flex-col lg:flex-row gap-4 px-8 py-4">
                {/* LEFT MAIN COLUMN */}
                <div className="w-full  flex flex-col lg:flex-row gap-4">
                  {/* SUB COLUMN 1 */}
                  <div className="w-full ">
                    <div
                      className={cn(
                        "border-[0.25px] border-slate-600 rounded-2xl",
                        "odz-table-row p-3 flex justify-between",
                        "bg-blue-100" // Alternating row colors
                      )}
                    >

                      <div className="text-xl font-secondary px-4 font-bold flex gap-4 items-center ">
                        <span className="cursor-pointer">
                          <NavigateWrapper>
                            <ArrowLeft />
                          </NavigateWrapper>
                        </span>
                        <span className="text-[#062393]">
                          {userData?.first_name} {userData?.last_name}
                        </span>
                      </div>
                      <div className="flex gap-2 items-center">

                        <p className="text-lg">User status </p>
                        <select
                          value={
                            userData?.is_verified === null
                              ? "pending"
                              : userData?.is_verified
                                ? "authorized"
                                : "unauthorized"
                          }
                          onChange={async (e) => {
                            const selectedValue = e.target.value;
                            const newValue =
                              selectedValue === "authorized"
                                ? true
                                : selectedValue === "unauthorized"
                                  ? false
                                  : null;

                            const confirmText =
                              newValue === true
                                ? "authorize"
                                : newValue === false
                                  ? "unauthorize"
                                  : "set as pending";

                            const confirmed = window.confirm(
                              `Are you sure you want to ${confirmText} this workspace?`
                            );

                            if (!confirmed) {
                              // Reset to previous value if cancelled
                              e.target.value =
                                userData?.is_verified === null
                                  ? "pending"
                                  : userData?.is_verified
                                    ? "authorized"
                                    : "unauthorized";
                              return;
                            }

                            // Proceed with API call
                            await handleUserStatusChange(userData?.id, newValue);
                          }}
                          className={cn(
                            "rounded-2xl px-2 py-1 border text-white",
                            userData?.is_verified == true
                              ? "bg-green-500"
                              : userData?.is_verified === false
                                ? "bg-red-500"
                                : "bg-orange-500"
                          )}
                        >
                          <option value="authorized" className="bg-white text-black">
                            Authorized
                          </option>
                          <option value="unauthorized" className="bg-white text-black">
                            UnAuthorized
                          </option>
                        </select>

                      </div>

                    </div>

                  </div>
                </div>
              </div>
            </Spacer>
            <Spacer>
              <div className="text-xl font-secondary px-8 font-bold text-[#062393]">Profile Information</div>
              <div className="flex flex-col gap-4 px-8 py-4">
                {(Array.isArray(userData) ? userData : [userData]).map((user, userIndex) => {

                  const allowedKeys = ["first_name", "last_name", "email", "country_code", "phone_number", "is_verified", "role_id", "started_on", 'secret_key'];
                  const entries = Object.entries(user).filter(([key]) => allowedKeys.includes(key));

                  // Split into 2 columns for layout
                  const middleIndex = Math.ceil(entries.length / 2);
                  const col1 = entries.slice(0, middleIndex);
                  const col2 = entries.slice(middleIndex);

                  return (
                    <div
                      key={userIndex}
                      className="flex flex-col lg:flex-row gap-4 rounded-2xl "
                    >
                      {/* Column 1 */}
                      <div className="w-full border-[0.25px] border-slate-600 rounded-2xl">
                        {col1.map(([key, value], idx) => (
                          <div
                            key={`${key}-${idx}`}
                            className={cn(
                              "odz-table-row p-3 flex justify-between ",
                              idx % 2 === 0 ? "bg-blue-100" : "bg-[#F6FBFF]"
                            )}
                          >
                            <p className="capitalize">{key.replace(/_/g, " ")}</p>
                            <p>{String(value)}</p>
                          </div>
                        ))}
                      </div>

                      {/* Column 2 */}
                      <div className="w-full border-[0.25px] border-slate-600 rounded-2xl">
                        {col2.map(([key, value], idx) => (
                          <div
                            key={`${key}-${idx}`}
                            className={cn(
                              "odz-table-row p-3 flex justify-between",
                              idx % 2 === 0 ? "bg-blue-100" : "bg-[#F6FBFF]"
                            )}
                          >
                            <p className="capitalize">{key.replace(/_/g, " ")}</p>
                            <p>{String(value)}</p>
                          </div>
                        ))}
                      </div>
                    </div>
                  );
                })}
              </div>
            </Spacer>


            <div className="text-xl font-secondary px-8 font-bold text-[#062393]"> Assigned Workspaces</div>
            <div
              className={cn("px-8 w-full rounded-2xl")}>
              <table className="min-w-full px-8 rounded-2xl border text-sm bg-background ">
                <thead>
                  <tr className="odz-table-row bg-[#020B2D] text-white hover:bg-[#020B2D] p-3">
                    {/* <th className="p-2 border">ID</th> */}
                    <th className="p-2 border">Workspaces</th>
                    <th className="p-2 border">Onboarding Date</th>
                    <th className="p-2 border">Activation Key</th>
                    {/* <th className="p-2 border">Authorized</th> */}
                  </tr>
                </thead>
                <tbody>
                  {workspaceData.map((workspace, idx) => (
                    <tr
                      key={workspace.id}
                      className={cn(
                        "hover:bg-accent",
                        idx % 2 === 0 ? "bg-blue-100" : "bg-[#F6FBFF]"
                      )}
                    >
                      {/* <td className="p-2 border">{workspace.id}</td> */}
                      <td className="p-2 border">{workspace.name}</td>
                      <td className="p-2 border">
                        {new Date(workspace.created_at).toLocaleString()}
                      </td>

                      <td className="p-2 border space-x-2 min-w-[10rem]">
                        {workspace.activation_key ? (
                          (() => {
                            const maxAttempts = 3;
                            const canCopy =
                              workspace.is_activated === true || workspace.attempts <= maxAttempts;

                            return (
                              <button
  // disabled={!canCopy || workspace.is_activated === false || workspace.is_activated === null}
  onClick={() => {
    // if (!canCopy || workspace.is_activated === false || workspace.is_activated === null) return;
    navigator.clipboard.writeText(workspace.activation_key || "");
    alert("Copied activation key to clipboard!");
  }}
  className={`w-60 p-2 rounded-full text-white flex items-center justify-center gap-2 ${
    workspace.is_activated === false
      ? "bg-red-500"
      : workspace.is_activated === null
      ? "bg-orange-500 cursor-not-allowed"
      : "bg-green-500"
  } ${showDanger(workspace) ? "border-2 border-red-700" : ""}`}
>
  {workspace.activation_key}
  <ClipboardCopy className="w-4 h-4" />
</button>

                            );
                          })()
                        ) : (
                          <ConfirmationAlert
                            title="Generate Activation Key"
                            description={
                              <div>
                                <p className="mb-2">
                                  Enter the Field Assistant ID to generate a key.
                                </p>
                                 <select
        className="w-full px-2 py-1 border rounded"
        value={fieldAssistantId}
        onChange={(e) => setFieldAssistantId(e.target.value)}
      >
        <option value="">Select Field Assistant</option>
        {fieldAssistants.map((user) => (
          <option key={user.id} value={user.id}>
            {user.first_name} {user.last_name} ({user.email})
          </option>
        ))}
      </select>
                                {/* <input
                                  type="number"
                                  className="w-full px-2 py-1 border rounded"
                                  placeholder="Field Assistant ID"
                                  value={fieldAssistantId}
                                  onChange={(e) => setFieldAssistantId(e.target.value)}
                                /> */}
                              </div>
                            }
                            onConfirm={() => handleGenerateKey(workspace.id)}
                            isLoading={loading}
                          >
                            <button className="w-60 px-2 py-1 bg-green-500 text-white rounded">
                              Generate Activation Key
                            </button>
                          </ConfirmationAlert>
                        )}
                      </td>
{/* 

                      <td className="p-2 border">
                        <div className="flex justify-center">

                          <td className="p-2">
                            <div className="flex justify-center">

                              <select
                                value={
                                  workspace.is_activated === null
                                    ? "pending"
                                    : workspace.is_activated
                                      ? "authorized"
                                      : "unauthorized"
                                }
                                onChange={async (e) => {
                                  const selectedValue = e.target.value;
                                  const newValue =
                                    selectedValue === "authorized"
                                      ? true
                                      : selectedValue === "unauthorized"
                                        ? false
                                        : null;

                                  const confirmText =
                                    newValue === true
                                      ? "authorize"
                                      : newValue === false
                                        ? "unauthorize"
                                        : "set as pending";

                                  const confirmed = window.confirm(
                                    `Are you sure you want to ${confirmText} this workspace?`
                                  );

                                  if (!confirmed) {
                                    // Revert to original value
                                    e.target.value =
                                      workspace.is_activated === null
                                        ? "pending"
                                        : workspace.is_activated
                                          ? "authorized"
                                          : "unauthorized";
                                    return;
                                  }

                                  await handleWorkspaceStatusChange(workspace.id, newValue);
                                }}
                                className={cn(
                                  "rounded-2xl px-2 py-1 border text-white",
                                  workspace.is_activated === true
                                    ? "bg-green-500"
                                    : workspace.is_activated === false
                                      ? "bg-red-500"
                                      : "bg-orange-500"
                                )}
                              >
                                <option value="authorized" className="bg-white text-black">
                                  Authorized
                                </option>
                                <option value="unauthorized" className="bg-white text-black">
                                  UnAuthorized
                                </option>
                              </select>

                            </div>
                          </td>

                        </div>
                      </td> */}
                    </tr>
                  ))}
                </tbody>

              </table>
            </div>
            {/* Hidden input used via modal */}
            <input
              type="text"
              value={fieldAssistantId}
              onChange={(e) => setFieldAssistantId(e.target.value)}
              placeholder="Field Assistant ID"
              className="hidden"
            />

          </form>
        </FormProvider>
      </>}
    </>

  );
};

export default UserCreationFormPage;