import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { authApi } from "../api/authApi";
import { logout } from "../store/authSlice";
import { toast } from "sonner";
import { ConfirmationAlert } from "@/components/ui/ConfirmationAlert";

interface NavigateWrapperProps {
  children: React.ReactNode;
}

const NavigateWrapper: React.FC<NavigateWrapperProps> = ({ children }) => {
  const dispatch = useDispatch();
  const [isLoading, setIsLoading] = useState(false);

  const handleLogout = async () => {
    window.history.back()
  };

  return (
    <ConfirmationAlert
      title="Leaving this page"
      description="Leaving this page without saving might cause data loose, because there are unsaved data changes"
      onConfirm={handleLogout}
      isLoading={isLoading}
      cancelButton="Don't save"
      successButton="Save changes"
    >
      {children}
    </ConfirmationAlert>
  );
};

export default NavigateWrapper;
